.game .title {
  font-family: 'Pacifico', cursive;
  font-size: 6rem;
}

.puzzle-block {
  background-color: transparent;
  border: 0;
  height: 132px;
  width: 132px;
  position: relative;
  float: left;
  z-index: 10;
}
.puzzle-block:focus {
  outline: none;
}

.game-board .board {
  height: 396px;
  width: 396px;
  position: relative;
}
.full-image {
  opacity: 0.2;
}
